.home-main {
    /* overflow:hidden; */
    width: 100%;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-main.active {
    margin-left: 200px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
    width: calc(100% - 200px);
}
.home-title-bg {
    top: 0%;
    max-height: 250px;
    width: 100%;
}
.home-title-text {
    position: absolute;
    top: 8%;
    left: 40%; 
    font-size: 50px;
    color: white;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-title-text.active {
    left: 50%;
}
.home-head-text {
    font-size: 30px;
    text-align: center;
}
.home-icons {
    display: inline-block;
    margin-left: 160px;
    margin-top: 30px;
}
.home-sidebar {
    position: fixed;
    width: 200px;
    height: 100%;
    background: #FFFFFF;
    left: -200px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-sidebar.active {
    left: 0px;
}
/* hamburger icon */
.home-burger {
    display: block;
    width: 30px;
    height: 3px;
    background: #FFFFFF;
    margin: 5px 0px;
}
.home-toggle-btn {
    position: absolute;
    left: 30px;
    top: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-toggle-btn.active {
    left: 230px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-nav {
    margin: 0px;
    padding: 0px;
}
.home-nav-text {
    list-style: none;
    padding: 30px 55px;
    font-size: 12px;
}
.home-no-hyperlink {
    text-decoration: none;
    color: black;
}
.home-profile-bg {
    width: 200px;
}
.home-profile-pic {
    position: absolute;
    left: -200px;
    top: 30px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-profile-pic.active {
    left: 10px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-home-icon {
    position: absolute;
    left: -200px;
    top: 226px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-home-icon.active {
    left: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-profile-icon {
    position: absolute;
    left: -200px;
    top: 300px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-profile-icon.active {
    left: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-search-icon {
    position: absolute;
    left: -200px;
    top: 374px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-search-icon.active {
    left: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-log-icon {
    position: absolute;
    left: -200px;
    top: 448px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-log-icon.active {
    left: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-out-icon {
    position: absolute;
    left: -200px;
    top: 522px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}
.home-out-icon.active {
    left: 20px;
    -webkit-transition: all 500ms linear;
    -o-transition: all 500ms linear;
    transition: all 500ms linear;
}






